import React from 'react'
import CardItem from './CardItem'

export default function index() {
  const cards=[
    {
      id:1,
      name:'',
      deadDate:'2022121'
    },
    {
      id:2,
      name:'',
      deadDate:'202361'
    },
    {
      id:3,
      name:'',
      deadDate:'202345'
    }
  ]
  return (
    <div style={{top:'-0px',position:'relative'}}>
      {
        cards.map(item =>(
          <CardItem key={item.id} name={item.name} date={item.deadDate}/>
        ))
      }
    </div>
  )
}
