import React from 'react'
import { List,NavBar } from 'antd-mobile'
import { useNavigate } from 'react-router-dom'

export default function ExpRec() {
    const navigate=useNavigate()
    const back=()=>{
        navigate('/tabs/Cards')
    }
    const handleClick= (prop)=>{
        navigate('/expDetail',{state:{
            moneyLeft:prop.moneyLeft,
            date:prop.date,
            cost:prop.cost,
            channel:prop.channel
        }})
        // console.log(prop)
    }
    const records=[
        {
            month:'20227',
            detail:[
                {
                    id:0,
                    channel:'',
                    moneyLeft:'100',
                    date:'2022730',
                    cost:'-10',
                },
                {
                    id:1,
                    channel:'',
                    moneyLeft:'110',
                    date:'2022720',
                    cost:'-10',
                },
                {
                    id:2,
                    channel:'',
                    moneyLeft:'120',
                    date:'202273',
                    cost:'-10',
                },
            ]
        },
        {
            month:'20226',
            detail:[
                {
                    id:0,
                    channel:'',
                    moneyLeft:'100',
                    date:'2022630',
                    cost:'-10',
                },
                {
                    id:1,
                    channel:'',
                    moneyLeft:'110',
                    date:'2022620',
                    cost:'-10',
                },
                {
                    id:2,
                    channel:'',
                    moneyLeft:'120',
                    date:'202263',
                    cost:'-10',
                },
            ]
        },
        {
            month:'20225',
            detail:[
                {
                    id:0,
                    channel:'',
                    moneyLeft:'100',
                    date:'2022530',
                    cost:'-10',
                },
                {
                    id:1,
                    channel:'',
                    moneyLeft:'110',
                    date:'2022520',
                    cost:'-10',
                },
                {
                    id:2,
                    channel:'',
                    moneyLeft:'120',
                    date:'202253',
                    cost:'-10',
                },
            ]
        },
        {
            month:'20224',
            detail:[
                {
                    id:0,
                    channel:'',
                    moneyLeft:'100',
                    date:'2022430',
                    cost:'-10',
                },
                {
                    id:1,
                    channel:'',
                    moneyLeft:'110',
                    date:'2022420',
                    cost:'-10',
                },
                {
                    id:2,
                    channel:'',
                    moneyLeft:'120',
                    date:'202243',
                    cost:'-10',
                },
            ]
        },
    ]
  return (
    <div>
        <NavBar onBack={back}></NavBar>
        {
            records.map(item=>(
                <List key={item.month}header={item.month} style={{fontSize:'xx-large',fontWeight:'bold'}}>
                    {
                        item.detail.map(i=>(
                            <List.Item key={i.id} title={i.channel}
                            description={i.date} extra={i.cost}
                            onClick={()=>handleClick(i)}
                            >{i.moneyLeft}
                            </List.Item>
                        ))
                    }
                </List>
            ))
        }
    </div>
  )
}
