import React from 'react'
import { useLocation,useNavigate} from 'react-router-dom'
import { NavBar,List,Button } from 'antd-mobile'
export default function ExpDetail() {
    const nav=useNavigate()
    const {state:{moneyLeft,date,channel,cost}}=useLocation()
    const back=()=>{
        nav('/expRec')
    }
    const handleClick=()=>{
      nav('/appeal')
    }
  return (
    <div>
      <NavBar onBack={back}></NavBar>
      <List style={{fontWeight:'bold',fontSize:'xx-large',height:'15vh'}}>
        <List.Item title='()' clickable>{cost}</List.Item>
        <List.Item extra='' ></List.Item>
      </List>
      <List mode='card'>
        <List.Item extra={date}></List.Item>
        <List.Item extra={moneyLeft}></List.Item>
        <List.Item extra={channel}></List.Item>
      </List>
      <Button block color='success' size='large' style={{margin:'10px'}}
      onClick={handleClick}></Button>
    </div>
  )
}
