import React from 'react'
import { SearchBar,Divider,Dropdown,Radio,Space,Card,Rate } from 'antd-mobile'
import {  RightOutline } from 'antd-mobile-icons'
import { useNavigate } from 'react-router-dom'
import "./demo2.css"
// 
function ShopItem(props){
  const nav=useNavigate()
  const headerClick=(prop)=>{
    // 
    // 
    nav('/shopDetail',{
      state:{
        name:prop.name,
        rate:prop.rate,
      }
    })
  }
  return (
    <Card title={props.name} extra={<RightOutline/>}
    onHeaderClick={()=>headerClick(props)}
    className="card">
      
      <br/>
      <Rate readOnly value={props.rate}></Rate>
    </Card>
  )
}
export default function index() {
  const shop=[
    {
      id:0,
      name:'xxx',
      rate:4,
    },
    {
      id:1,
      name:'yyy',
      rate:4.5,
    },
    {
      id:2,
      name:'xx',
      rate:2,
    },
    {
      id:3,
      name:'xxx',
      rate:1.5,
    }
  ]
  return (
    <div>
       <SearchBar
          placeholder=''
          showCancelButton
          style={{
            '--border-radius': '100px',
            '--background': '#ffffff',
            '--height': '42px',
            '--padding-left': '20px',
          }}
        />
         <Dropdown style={{fontSize:'xx-large'}}>
          <Dropdown.Item key='sorter' title='' >
            <div style={{ padding: 12 }}>
              <Radio.Group defaultValue='default'>
                <Space direction='vertical' block>
                  <Radio block value='default'>
                    
                  </Radio>
                  <Radio block value='nearest'>
                    
                  </Radio>
                  <Radio block value='top-rated'>
                    
                  </Radio>
                </Space>
              </Radio.Group>
            </div>
          </Dropdown.Item>
        </Dropdown>
        <Divider style={{fontSize:'large',color:'black'}}></Divider>
          {
            shop.map(item=>(
              <ShopItem key={item.id} name={item.name} rate={item.rate}/>
            ))
          }
    </div>
  )
}
