import React from 'react'
import { Card,Button } from 'antd-mobile'
import './demo2.less'
import { useNavigate } from 'react-router-dom'

export default function CardItem(props) {
  const navigate=useNavigate()
  const card2Exp=()=>{
    navigate('/expRec')
  }
  const card2CheckOut=()=>{
    navigate('/checkOut')
  }
  const card2Contract=()=>{
    navigate('/showContract')
  }
  return (
    <div>
      <Card title={
        <div style={{fontWeight:'bold',fontSize:'x-large'}}>
          {props.name}
        </div>
      }
      className='card'
      >
        <div>
          :100
        </div>
        {/* div */}
        {/* <div className="content"><span>:{props.date}</span></div>
        <div className="content"><span>:{props.date}</span></div> */}
        <div className='footer'>
        <Button color='success'style={{right:'10px'}} onClick={card2Contract}>
            
          </Button>
          <Button color='primary'style={{right:'5px'}} onClick={card2Exp}>
            
          </Button>
          <Button color='warning'onClick={card2CheckOut}>
            
          </Button>
        </div>
      </Card>
    </div>
  )
}
